.light-mode {
    background-color: #F0EDEE;
    color: black;
  }
  
.dark-mode {
    background-color: #0D1321;
    color: white;
}

:root {
    --even-bg: #ffffff;
    --odd-bg: #f0f0f0;
    --text-color: #000000;
  }
  
[data-theme='dark'] {
    --even-bg: #1D2D44;
    --odd-bg: #3E5C76;
    --text-color: #ffffff;
}

.even {
    background-color: var(--even-bg);
    color: var(--text-color);
  }
  
.odd {
    background-color: var(--odd-bg);
    color: var(--text-color);
}

.buttons-div {
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 20px;
    right: 200px;
    height: 8vh;
    overflow: hidden;
    align-items: center;
    justify-content: center;
}

.instructions-p {
    line-height: 26px;
}

.mode-toggle {
    border-radius: 10px;
}

.mode-toggle:hover {
    cursor: pointer;
}

img {
    border-radius: 10px;
}

.font-size {
    height: 3vh;
    margin-top: 4px;
}

.font-button {
    background-color: none;
    height: 30px;
    border-radius: 15px;
    overflow: hidden;
    margin-top: 0px;
}

#-button {
    padding-left: 7px;
    padding-right: 7px;
}

.font-button:hover {
    cursor: pointer;
}

.font-buttons-div {
    display: flex;
    margin-top: 0px;
    overflow: hidden;
}

.mode-toggle-div {
    overflow: hidden;
    padding-bottom: 10px;
}

a {
    width: 50px;
    height: 50px;
    overflow: hidden;
}

img {
    width: 150px;
}

.kofi-div {
    overflow: hidden;
    width: 150px;
    height: 80px;
    right: 35px;
    top: 10px;
    position: absolute;
}

.App {
    width: auto;
    height: 100vh;
}

body {
    width: 100vw;
    height: 100vh;
}

.components {
    margin-left: 20px;
    margin-top: 10px;
}

.instructions-title {
    margin-bottom: -10px;
}

.instructions {
    padding-bottom: 10px;
    margin-top: -50px;
    margin-left: -5px;
}

.landing-page {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 98vh;
    flex-direction: column;
    overflow: hidden;
}

.animation-div {
    position: absolute;
    margin-left: -80px;
}

.failure {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
}

.icons-div {
    display: flex;
    flex-direction: row;
    margin-top: -10px;
    margin-left: 25px;
}

.social-gift-div {
    display: flex;
    width: 100vw;
    align-items: center;
    justify-content: center;
    margin-left: -25px;
    
}

.messages-div {
    margin-left: 20px;
    margin-top: 20px;
}

h1 {
    font-size: 25px;
}

.font-size {
    padding: 7px;
    padding-top: 0px;
}

* {
    font-family: 'Open Sans', sans-serif;
}

@media (250px < width < 800px) {
    .App {
        height: auto;
        width: auto;
        overflow: hidden;
        overflow-x: hidden;
    }

    h1 {
        font-size: 18px;
    }

    .container {
        height: fit-content;
        width: 100vw;
        margin-right: -20px;
        padding-bottom: -60px;
    }

    .light-mode {
        overflow: hidden;
        overflow-x: hidden;
    }

    .dark-mode {
        overflow: hidden;
        overflow-x: hidden;
    }

    .kofi-img {
        position: absolute;
        width: 100px;
        overflow: hidden;
        overflow-x: hidden;
        right: 0;
        margin-right: 10px;
    }

    .mode-toggle {
        font-size: 10px;
        overflow: hidden;
        overflow-x: hidden;
    }

    .landing-page {
        overflow: hidden;
        overflow-x: hidden;
    }

    .buttons-div {
        display: flex;
        flex-direction: column;
        position: absolute;
        top: -25px;
        right: 105px;
        height: 100px;
        overflow: hidden;
        align-items: center;
        justify-content: center;
        width: 100px;
        font-size: 10px;
        overflow-x: hidden;
        z-index: 1;
    }

    .font-size {
        font-size: 12px;
        width: 30px;
        overflow: hidden;
        height: 50px;
        text-align: center;
        margin-left: -5px;
        margin-right: -5px;
        overflow-x: hidden;
    }

    .font-buttons-div {
        align-items: center;
        margin-top: -5px;
        position: absolute;
        top: 55px;
        height: 80px;
    }

    .font-button {
        margin-top: -30px;
        overflow: hidden;
        overflow-x: hidden;
        height: 25px;
    }

    .kofi-div {
        overflow: hidden;
        width: 200px;
        height: 80px;
        top: 10px;
        position: absolute;
        right: 0;
        z-index: 0;
    }

    * {
        overflow: hidden;
        overflow-x: hidden;
    }

    .messages-div {
        margin-top: -10px;
    }

    .components {
        margin-left: -17px;
    }

    .chat-div {
        margin-left: 3px;
    }

    .icons-div {
        flex-direction: row;
    }
}