.diamond-comp {
    display: flex;
    padding-right: 10px;
}

.diamond-icon {
    width: 30px;
    padding-right: 10px;
}

@media (300px < width < 800px) {
    .diamond-comp {
        flex-direction: column;
        align-items: center;
        justify-content: center;
        text-align: center;
        margin-top: -3px;
    }

    .diamond-icon {
        width: 25px;
        margin-left: 5px;
        margin-top: 13px;
        
    }
}