.like-comp {
    display: flex;
    padding-right: 50px;
    padding-left: 40px;
}

.like-icon {
    width: 40px;
    padding-right: 8px;
}

@media (300px < width < 800px) {
    .like-comp {
        flex-direction: column;
        align-items: center;
        justify-content: center;
        text-align: center;
        padding-left: 10px;
        padding-right: 20px;
        margin-top: -5px;
    }

    .like-icon {
        width: 35px;
        margin-left: 5px;
        margin-top: 10px;
    }

    .like-count {
        margin-top: -4px;
        margin-left: -5px;
        font-size: 16px;
    }
}