:root {
    --even-bg: #ffffff;
    --odd-bg: #f0f0f0;
    --text-color: #000000;
  }
  
[data-theme='dark'] {
    --even-bg: #1D2D44;
    --odd-bg: #3E5C76;
    --text-color: #ffffff;
}

.even {
    background-color: var(--even-bg);
    color: var(--text-color);
  }
  
.odd {
    background-color: var(--odd-bg);
    color: var(--text-color);
}

* {
    overflow-x: hidden;
}

.chat-div {
    margin: 0 auto;
}

.chat-container {
    overflow-y: scroll;
    height: 56vh;
    width: 96vw;
    border-radius: 10px;
}

h1 {
    margin: 0 auto;
}

