.viewer-comp {
    display: flex;
    padding-right: 10px;
}

.viewer-icon {
    width: 35px;
    padding-right: 8px;
}

@media (300px < width < 800px) {
    .viewer-comp {
        flex-direction: column;
        align-items: center;
        justify-content: center;
        text-align: center;
        padding-left: 5px;
        margin-top: 7px;
        margin-bottom: 10px;
    }

    .viewer-icon {
        margin-left: 5px;
        margin-top: 10px;
        width: 30px;
    }

    .count {
        margin-top: 0px;
        margin-left: -5px;
        font-size: 16px;
    }
}